import moment from "moment";

export const home = () => {
  return {
    type: "home",
  };
};

export const NHL = () => {
  return {
    type: "NHL",
  };
};

export const NBA = () => {
  return {
    type: "NBA",
  };
};

export const NFL = () => {
  return {
    type: "NFL",
  };
};

export const MLB = () => {
  return {
    type: "MLB",
  };
};

export const fetchTopNews = () => {
  return async (dispatch) => {
    dispatch({
      type: "FETCH_TOP_NEWS_REQUEST",
    });

    const info = await fetch(
      `https://newsapi.org/v2/top-headlines?country=ca&category=sports&apiKey=${process.env.REACT_APP_NEWS_API_KEY}`
    );

    const news = await info.json();

    dispatch({
      type: "FETCH_TOP_NEWS",
      payload: news,
    });
  };
};

let date = moment().subtract(1, "days").format("YYYY-MM-DD");

export const fetchNHLNews = () => {
  return async (dispatch) => {
    dispatch({
      type: "FETCH_NHL_NEWS_REQUEST",
    });

    const info = await fetch(
      `https://newsapi.org/v2/everything?language=en&excludeDomains=youtube.com, uni-watch.com&q=hockey AND nhl&from=${date}&sortBy=relevancy&apiKey=${process.env.REACT_APP_NEWS_API_KEY}`
    );
    const news = await info.json();
    dispatch({
      type: "FETCH_NHL_NEWS",
      payload: news,
    });
  };
};

